<template>
    <div>
        <label for="">{{ label }}</label>
        <input type="text" :name="name" :id="id" :value="value" class="input-item w-full py-2 px-3 rounded-lg mt-2 border"
            @input="$emit('value-input', $event.target.value)" :placeholder="placeholder">
    </div>
</template>
<script>
export default ({
    props: {
        label: String,
        name: String,
        placeholder: String,
        value: String,
        id: String
    }
})
</script>
<style scoped>
.input-item {
    /* Estilos generales del input */
    transition: border-color 0.3s;
}

.input-item:focus {
    /* Estilos cuando el input tiene el foco */
    outline: none;
    /* Para quitar el contorno predeterminado del navegador */
    box-shadow: 0 0 5px rgba(255, 171, 114, 0.5);
    /* Sombra alrededor del borde cuando está enfocado */
}
</style>