<template>
    <table class="w-full text-xs text-left text-gray-800">
        <thead class="text-xs text-gray-700 uppercase bg-gray-100">
            <tr>
                <th scope="col" class="px-6 py-3">
                    Cliente
                </th>
                <th scope="col" class="px-6 py-3">
                    RUC
                </th>
                <th scope="col" class="px-6 py-3">
                    Razón social
                </th>
                <th scope="col" class="px-6 py-3">
                    Codigo cliente
                </th>
                <th scope="col" class="px-6 py-3">
                    Teléfono
                </th>
                <th scope="col" class="px-6 py-3">
                    Dirección
                </th>
                <th scope="col" class="px-6 py-3">
                    Correo
                </th>
                <th scope="col" class="px-6 py-3">
                    Acciones
                </th>
            </tr>
        </thead>
        <tbody>
            <tr v-if="listClientes.length === 0">
                <td class="border px-4 py-4 text-xs text-center" colspan="8">No se encontró ningún dato
                    para esta tabla</td>
            </tr>
            <tr v-else v-for="cliente in listClientes" :key="cliente.id"
                class="bg-white border-b hover:bg-gray-50 ease-in duration-300">
                <td class="px-6 py-3">
                    {{ cliente.cliente }}
                </td>
                <td class="px-6 py-3">
                    {{ cliente.ruc }}
                </td>
                <td class="px-6 py-3">
                    {{ cliente.razon_social }}
                </td>
                <th scope="row" class="px-6 py-3 font-medium text-gray-900 whitespace-nowrap">
                    {{ cliente.codcliente }}
                </th>
                <td class="px-6 py-3">
                    {{ cliente.telefono }}
                </td>
                <td class="px-6 py-3">
                    {{ cliente.direccion }}
                </td>
                <th scope="row" class="px-6 py-3 font-medium text-gray-900 whitespace-nowrap">
                    {{ cliente.correo_electronico }}
                </th>
                <td class="px-6 py-2 flex items-center h-full gap-2">
                    <button
                        class="font-medium py-[0.5rem] px-3 rounded-lg bg-green-500 hover:bg-green-600 ease-in duration-300 text-white"
                        @click.prevent="editarCliente(cliente.id)">Editar</button>
                    <button
                        class="font-medium py-[0.5rem] px-3 rounded-lg bg-red-500 hover:bg-red-600 ease-in duration-300 text-white "
                        @click.prevent="eliminarCliente(cliente.id)">Eliminar</button>
                </td>
            </tr>
        </tbody>
    </table>
</template>
<script>
export default ({
    name: "TableClientes",
    props: {
        listClientes: Array
    },
    methods: {
        eliminarCliente(idCliente) {
            this.$emit('eliminar-cliente', idCliente)
        },
        editarCliente(idCliente) {
            this.$emit('editar-cliente', idCliente)
        }
    }
})
</script>