<template>
    <div class="fixed inset-0 flex items-center justify-center z-[9999]">
        <div class="absolute inset-0 bg-black opacity-30 pointer-events-none"></div>
        <!-- Container modal -->
        <div class="relative z-10 p-4 bg-white rounded-xl lg:min-w-[350px] lg:max-w-[450px] min-w-[330px] max-w-[350px]">
            <form class="p-2 text-gray-700 w-full h-full text-sm">
                <h3 class="text-center font-bold text-lg">Editar cliente</h3>
                <ItemForm @value-input="saveCliente" label="Cliente" name="cliente" placeholder="Nombre del cliente"
                    :value="cliente" />
                <ItemForm @value-input="saveRuc" label="RUC" name="ruc" placeholder="Ruc del cliente" :value="ruc" />
                <ItemForm @value-input="saveRazonsocial" label="Razón social" name="razon_social"
                    placeholder="Razón social del cliente" :value="razonSocial" />
                <ItemForm @value-input="saveCodigoCliente" label="Codigo de cliente" name="codigo_cliente"
                    placeholder="Codigo del cliente" :value="codigoCliente" />
                <!-- Dropdown para la sección Ubicación -->
                <div class="dropdown">
                    <button
                        class="dropdown-toggle bg-orange-400 flex justify-between items-center w-full rounded-lg text-white px-5 py-2 mt-2"
                        @click.prevent="toggleUbicacion" :class="{ 'bg-orange-500': mostrarUbicacion }">
                        <span>Datos ubicación</span>
                        <i class="fa-solid fa-angle-down"></i>
                    </button>
                    <div class="dropdown-menu mx-2 px-2 py-3 rounded-md bg-slate-200 transition-max-height duration-300"
                        :class="{
                            'max-h-[500px]': mostrarUbicacion,
                            'max-h-0': !mostrarUbicacion,
                            'overflow-hidden': !mostrarUbicacion,
                            'pt-0 pb-0': !mostrarUbicacion
                        }">
                        <ItemForm @value-input="saveDireccion" label="Dirección" name="direccion"
                            placeholder="Dirección del cliente" :value="direccion"/>
                        <ItemForm @value-input="saveUbigeo" label="Ubigeo" name="ubigeo" placeholder="Ubigeo del cliente" :value="ubigeo"/>
                    </div>
                </div>

                <!-- Dropdown para la sección Contacto -->
                <div class="dropdown">
                    <button
                        class="dropdown-toggle bg-orange-400 flex justify-between items-center w-full rounded-lg text-white px-5 py-2 mt-2"
                        @click.prevent="toggleContacto" :class="{ 'bg-orange-500': mostrarContacto }">
                        <span>Datos contacto</span>
                        <i class="fa-solid fa-angle-down"></i>
                    </button>
                    <div class="dropdown-menu mx-2 px-2 py-3 rounded-md bg-slate-200 transition-max-height duration-300"
                        :class="{
                            'max-h-[500px]': mostrarContacto,
                            'max-h-0': !mostrarContacto,
                            'overflow-hidden': !mostrarContacto,
                            'pt-0 pb-0': !mostrarContacto
                        }">
                        <ItemForm @value-input="saveCorreo" label="Correo" name="correo" placeholder="Correo del cliente" :value="correo"/>
                        <ItemForm @value-input="saveTelefono" label="Teléfono" name="telefono"
                            placeholder="Teléfono del cliente" :value="telefono" />
                    </div>
                </div>
            </form>
            <div class="btns flex gap-2 justify-center items-center pt-2">
                <button @click="closeModal"
                    class="bg-gray-400 hover:bg-gray-500 text-white text-sm p-2 px-8 rounded-xl transition duration-300">Cancelar</button>
                <button @click="saveDatos"
                    class="bg-blue-400 hover:bg-blue-500 text-white text-sm p-2 px-8 rounded-xl transition duration-300">Actualizar</button>
            </div>
        </div>
        <!-- Container modal -->
    </div>
</template>
  
<script>
import { ref, onMounted } from 'vue';
import { buscarCliente } from '../../services/ClientesService';
import ItemForm from '../generales/ItemForm.vue';
export default {
    name: 'ModalEditar',
    components: { ItemForm },
    props: {
        idCliente: String
    },
    emits: ['cancelar-editar', 'aceptar-editar'],
    setup(props, { emit }) {
        const cliente = ref('');
        const ruc = ref('');
        const razonSocial = ref('');
        const codigoCliente = ref('');
        const direccion = ref('');
        const correo = ref('');
        const telefono = ref('');
        const ubigeo = ref('');
        const mostrarUbicacion = ref(false);
        const mostrarContacto = ref(false);

        const toggleUbicacion = () => {
            mostrarUbicacion.value = !mostrarUbicacion.value;
        }
        const toggleContacto = () => {
            mostrarContacto.value = !mostrarContacto.value;
        }
        const closeModal = () => {
            emit('cancelar-editar')
        }
        const saveCliente = (value) => {
            cliente.value = value;
        }
        const saveRuc = (value) => {
            ruc.value = value;
        }
        const saveRazonsocial = (value) => {
            razonSocial.value = value;
        }
        const saveCodigoCliente = (value) => {
            codigoCliente.value = value;
        }
        const saveDireccion = (value) => {
            direccion.value = value;
        }
        const saveUbigeo = (value) => {
            ubigeo.value = value;
        }
        const saveCorreo = (value) => {
            correo.value = value
        }
        const saveTelefono = (value) => {
            telefono.value = value
        }
        const saveDatos = () => {
            emit('aceptar-editar', {
                cliente: cliente.value,
                ruc: ruc.value,
                razonSocial: razonSocial.value,
                codigoCliente: codigoCliente.value,
                direccion: direccion.value,
                ubigeo: ubigeo.value,
                correo: correo.value,
                telefono: telefono.value
            })
        }
        onMounted(() => {
            buscarCliente(props.idCliente)
                .then(response => {
                    cliente.value = response.data.cliente[0].cliente;
                    ruc.value = response.data.cliente[0].ruc;
                    razonSocial.value = response.data.cliente[0].razon_social;
                    codigoCliente.value = response.data.cliente[0].codcliente;
                    direccion.value = response.data.cliente[0].direccion;
                    ubigeo.value = response.data.cliente[0].ubigeo;
                    correo.value = response.data.cliente[0].correo_electronico;
                    telefono.value = response.data.cliente[0].telefono;
                })
        })
        return {
            closeModal,
            toggleUbicacion,
            toggleContacto,
            mostrarUbicacion,
            mostrarContacto,
            saveCliente,
            saveRuc,
            saveRazonsocial,
            saveCodigoCliente,
            saveDireccion,
            saveCorreo,
            saveTelefono,
            saveUbigeo,
            saveDatos,
            cliente,
            ruc,
            razonSocial,
            codigoCliente,
            direccion,
            ubigeo,
            correo,
            telefono
        }
    },
};
</script>

