import axios from 'axios';

const baseURL = process.env.VUE_APP_BASE_URL;

export function listarClientes(codempresa) {
    return axios.get(`${baseURL}clientes/list?codempresa=${codempresa}`)
}

export function crearClientes(dataformulario, codempresa) {
    return axios.post(`${baseURL}clientes/create`, {
        codempresa: codempresa,
        codcliente: dataformulario.codigoCliente,
        cliente: dataformulario.cliente,
        ruc: dataformulario.ruc,
        razon_social: dataformulario.razonSocial,
        ubigeo: dataformulario.ubigeo,
        direccion: dataformulario.direccion,
        correo_electronico: dataformulario.correo,
        telefono: dataformulario.telefono
    })
}

export function eliminarClientes(idCliente) {
    return axios.delete(`${baseURL}clientes/delete?idCliente=${idCliente}`)
}

export function buscarCliente(idCliente){
    return axios.get(`${baseURL}clientes/search?idCliente=${idCliente}`)
}

export function editarClientes(idCliente, datacliente) {
    return axios.put(`${baseURL}clientes/edit`, {
        id: idCliente,
        cliente: datacliente.cliente,
        ruc: datacliente.ruc,
        razon_social: datacliente.razonSocial,
        ubigeo: datacliente.ubigeo,
        direccion: datacliente.direccion,
        correo_electronico: datacliente.correo,
        telefono: datacliente.telefono
    })
}