<template>
    <section class="min-h-full w-full pb-[5rem] lg:pb-[2rem]">
        <div class="mb-8 flex justify-between items-center">
            <h3 class="title-vista text-2xl font-semibold text-neutral-600">Administrar clientes</h3>
            <button @click="openModal"
                class="py-2 px-3 rounded-xl bg-orange-400 text-white text-sm hover:bg-orange-500 transition duration-300">
                Crear clientes
            </button>
        </div>
        <div class="overflow-x-auto shadow-md sm:rounded-lg">
            <TableClientes :listClientes="listClientes" @eliminar-cliente="eliminarCliente"
                @editar-cliente="editarCliente" />
        </div>
        <transition name="modal" mode="out-in">
            <ModalCreate v-show="openModalV" @close-modal="closeModal" @save-datos="saveDatos" />
        </transition>
        <transition name="modal" mode="out-in">
            <ModalDelete v-if="confirmDelete" @aceptar-eliminar="aceptarEliminar" @cancelar-eliminar="cancelarEliminar"
                pregunta="¿Estás seguro de eliminar a este cliente?" />
        </transition>
        <transition name="modal" mode="out-in">
            <ModalEditar v-if="openModalEdit" @aceptar-editar="aceptarEditar" @cancelar-editar="cancelarEditar"
                :idCliente="idCliente" />
        </transition>
        <transition name="modal" mode="out-in">
            <ModalError v-if="openModalError" title="Advertencia" mensaje="Se debe rellenar todos los campos"
                @close-modal="closeModalError" />
        </transition>
        <transition name="modal" mode="out-in">
            <ConfirmEdit v-if="openSuccessEdit" title="Logrado" mensaje="Se ha editado correctamente"
                @close-modal="closeSuccessEdit" />
        </transition>
    </section>
</template>
<script>
import { ref, onMounted } from 'vue';
// eslint-disable-next-line
import { listarClientes, crearClientes, eliminarClientes, editarClientes } from '../services/ClientesService';
import TableClientes from '../components/clientes/TableClientes';
import ModalCreate from '../components/clientes/ModalCreate';
import ModalError from '../components/generales/ModalError.vue';
import ModalDelete from '../components/generales/ModalDelete.vue';
import ConfirmEdit from '../components/generales/ConfirmEdit.vue';
import ModalEditar from '../components/clientes/ModalEditar';
import store from '../store'

export default ({
    name: "AdminClientes",
    components: { TableClientes, ModalCreate, ModalDelete, ModalEditar, ModalError, ConfirmEdit },
    setup() {
        const openModalV = ref(false);
        const openModalEdit = ref(false);
        const openModalError = ref(false);
        const confirmDelete = ref(false);
        const openSuccessEdit = ref(false);
        const listClientes = ref([]);
        const idCliente = ref('')
        const openModal = () => {
            openModalV.value = true;
        };

        const closeModal = () => {
            openModalV.value = false;
        }

        const closeModalError = () => {
            openModalError.value = false;
        }

        const closeSuccessEdit = () => {
            openSuccessEdit.value = false;
            window.location.reload();
        }

        const eliminarCliente = (idcliente) => {
            idCliente.value = idcliente
            confirmDelete.value = true;
        }

        const aceptarEliminar = () => {
            eliminarClientes(idCliente.value)
                .then(response => {
                    console.log(response.data)
                    confirmDelete.value = false;
                    idCliente.value = '';
                    window.location.reload();
                })
                .catch((error) => {
                    console.log(error)
                })
        }

        const cancelarEliminar = () => {
            confirmDelete.value = false;
        }

        const cancelarEditar = () => {
            openModalEdit.value = false;
        }
        const editarCliente = (idcliente) => {
            idCliente.value = idcliente;
            openModalEdit.value = true;
        }

        const aceptarEditar = (dataformulario) => {
            if (dataformulario.codigoCliente == '' || dataformulario.cliente == '' || dataformulario.ruc == '' || dataformulario.razonSocial == '' ||
                dataformulario.ubigeo == '' || dataformulario.direccion == '' || dataformulario.correo == '' || dataformulario.telefono == '') {
                openModalEdit.value = false;
                openModalError.value = true;
            } else {
                editarClientes(idCliente.value, dataformulario)
                    .then(response => {
                        console.log(response.data)
                        openModalEdit.value = false;
                        openSuccessEdit.value = true;
                    })
            }
        }

        const saveDatos = (dataformulario) => {
            if (dataformulario.codigoCliente == '' || dataformulario.cliente == '' || dataformulario.ruc == '' || dataformulario.razonSocial == '' ||
                dataformulario.ubigeo == '' || dataformulario.direccion == '' || dataformulario.correo == '' || dataformulario.telefono == '') {
                openModalV.value = false;
                openModalError.value = true;
            } else {
                crearClientes(dataformulario, store.state.codempresa)
                    .then(response => {
                        console.log(response.data)
                        openModalV.value = false;
                        window.location.reload();
                    })
            }
        }
        onMounted(() => {
            listarClientes(store.state.codempresa)
                .then(response => {
                    listClientes.value = response.data.clientes
                })
        })
        return {
            listClientes,
            openModal,
            openModalV,
            openModalError,
            closeModal,
            closeSuccessEdit,
            openSuccessEdit,
            closeModalError,
            saveDatos,
            confirmDelete,
            eliminarCliente,
            aceptarEliminar,
            cancelarEliminar,
            cancelarEditar,
            editarCliente,
            openModalEdit,
            idCliente,
            aceptarEditar
        }
    }
})
</script>
<style scoped>
.modal-enter-active,
.modal-leave-active {
    transition: opacity 0.3s;
}

.modal-enter-from,
.modal-leave-to {
    opacity: 0;
}
</style>